.top-bar {
    background-color: #222426; 
    color: #ffffff;
    padding: 10px 0;
    font-family: 'Poppins', sans-serif;
    display: flex;
    justify-content: flex-end;
}

.top-bar nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.top-bar nav ul li {
    display: inline;
    margin-right: 20px;
}

.top-bar nav ul li a {
    text-decoration: none;
    color: #ffffff;
    font-weight: bold;
}

.top-bar nav ul li a:hover {
    text-decoration: underline;
}
